.catalog-cards {
    &__inner {
        margin: 30px 0;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between; } }
.catalog-card {
    display: flex;
    flex-direction: column;
    width: 270px;
    height: 450px;
    border-radius: 5px;
    background-color: #ffffff;
    overflow: hidden;
    &__img {
        width: 270px;
        height: 260px; }
    &__info {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 15px 18px;
        height: 190px;
        box-sizing: border-box; }

    &__title {
        font-size: 16px;
        line-height: 1.13;
        color: #644733; }
    &__text {
        margin-top: 4px;
        height: 89px;
        font-size: 15px;
        line-height: 1.2;
        color: #b3b3b3; }
    &__price {
        margin-top: auto;
        font-size: 16px;
        color: #644733; }
    &__favorite {
        position: absolute;
        bottom: 10px;
        right: 10px;
        width: 34px;
        height: 31px;
        background: url('../img/fav.svg') no-repeat 50% 50%;
        cursor: pointer;
        &_active {
            background: url('../img/fav-active.svg') no-repeat 50% 50%; } } }

